    border: none;
    background: none;
    color: white;
    background-color: #2951f1;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    /* align-items: center; */
    /* margin-right: auto; */
    font-size: 20px;
    /* font-weight: 300; */
    /* text-align: center; */
