@media only screen and (max-width: 767px) {
    input {
        font-size: 18px;
        width: 60%;
        padding-left: 2%;
    }

    body {}

    #logo {
        width: 156px;
        max-width: 100%;
        margin: 5px 20px;
    }

    #logo+div {
        display: flex;
    }

    .walletButton {
        margin: 5px auto;
        height: 50px;
        padding: 0px 0px;
        min-width: 170px
    }

    #searchbar {
        display: inline-block;
    }

    #accordionExample {
        width: 100%;
    }

    #copyright {
        padding-left: 0%;
        float: left;
        width: 45%;
        margin-bottom: 15px;
    }

    #contactus,
    #aboutus {
        float: left;
        width: 25%;
        margin-top: inherit;
    }

    #contactus a {}

    #contactus {
        width: 32%;
        text-align: initial;
        margin-top: -3px;
    }

    #aboutus {
        width: 34%;
        text-align: center;
    }

    .footer {
        font-size: 15px;
        height: 8rem;
        padding-top: 20px;
        font-size: smaller;
    }

    #flex h3 {
        font-size: 0.8em;
        width: 110px;
        margin-top: 0px;
        padding-top: 15px;
    }

    #flex {
        height: 45px;
    }

    #greentext {
        width: 60%;
        font-size: 1.8em;
        float: left;
        margin-left: 25px;
    }

    #buyfrom,
    #error {
        color: #ef1111;
        font-size: 14px;
        font-weight: 600;
        text-align: center;
    }

    .bluearea h1 {
        padding-bottom: 20px;
    }

    .bluearea {
        padding-top: 20px;
        padding-bottom: 2%;
        background-color: #2044d5;
        color: #f5f2eb;
    }

    #root button {
        min-width: 130px;
        font-size: 16px;
        height: 50px;
    }

    #publish {
        min-width: 110px !important;
        font-size: 18px !important;
        height: 70px !important;
    }

    #copyright {
        padding-left: 0%;
        float: left;
        width: 34%;
        margin-bottom: 15px;
    }
}